<template>
  <!--订单信息-->
  <div>
    <div>
      <div class="spring-scroller">
        <header class="per_center bgImg01bg mb0 pb2">
          <div class="pre_title">
            <router-link class="icon-Artboard7" to="/personal"></router-link>
          </div>
          <!--待支付 非人民币在线付-->
          <template v-if="state===0&&model.currencyKey!=1">
            <p class="To_travel_time ">订单已提交，等待付款</p>
            <p class="fnt4 To_travel_addMsg "><i class="icon-Artboard18"></i>目前仅支持人民币在线交易,请联系酒店线下付款</p>
          </template>
          <!--待支付 待确认 已确认 已取消 已挂账-->
          <p v-else class="To_travel_time">{{stateList[state]}}</p>
        </header>

        <div v-show="first">
        <div href="javascript:void(0)" class="a_link p5 no-active mb25">
          <p class="To_travel_p01 " id="cancel_order">
          <span>
             订单金额<i class="colorRMB"><i class="ft1">{{model.currency}}</i>{{model.orderSum}}</i>
          </span>
            <i class="" @click="iScostDetail=true">费用明细 <em class="icon-Artboard6"></em></i>
          </p>
        </div>
        <h3 class="To_travel_h301 fnt2">
          预订信息
        </h3>
        <dl class="bg To_travel_dl01 bg01">
          <div class="preMsg">
            <div class="preMsg_l">
              <img :src="model.hotelMainImgUrl" alt="" @error="errorLoadImg">
            </div>
            <div class="preMsg_r">
              <div class="preMsg_r_text">
                <h4>{{model.hotelName}}</h4>
                <p class="fnt3 colorA1">{{model.hotelAddress}}</p>
              </div>
              <div class="preMsg_r_a">
                <span class="J_card_btn" @click="isCall=true">联系酒店</span>
                <span class="J_img" @click="getMap">地图导航</span>
              </div>
            </div>
          </div>

          <div class="order_num pre_order">
            <h4 class="mt3">{{model.pricePlanName}}</h4>
            <div class="order_num_bottom clearfix">
              <div class="fl jing_price ">
                <p>{{model.checkIndate|dateFilter}}入住 - {{model.checkOutDate|dateFilter}}离店<i>{{roomNight?roomNight:''}}</i>晚<i>{{model.roomQuantity}}</i>间</p>
                <p>{{model.bedDesc}} <i v-if="model.bedDesc">|</i>
                  {{model.roomRateDetailDTOList&&model.roomRateDetailDTOList[0].breakfastName}}</p>
              </div>
            </div>
            <div class="preMsgList">
              <div><span>入住人</span>
                <p><em v-for="ite in model.guestDTOList">{{ite.guestName}}</em></p></div>
              <div v-if="model.userType==1"><span>联系人</span>
                <p>{{model.contacterDTO&&model.contacterDTO.contactName}}</p></div>
              <div><span>联系电话</span>
                <p>{{model.contacterDTO&&model.contacterDTO.contactPhone}}</p></div>
            </div>
          </div>

          <div class="preMsgList mb4 bt1"
               v-if="model.specialRequestDTOList||model.additionChargeDTOList&&model.additionChargeDTOList.length!=0">
            <div v-if="model.specialRequestDTOList"><span>特殊要求</span>
              <p v-for="item in model.specialRequestDTOList">{{item.content}}</p>
            </div>
            <div v-if="model.additionChargeDTOList.length!=0">
              <span>附加项</span>
              <p class="addtion-style" :class="idx!=0?'p2':''" v-for="(item,idx) in model.additionChargeDTOList">
                <i>{{item.name}}</i>
                <i>{{item.num}}{{unitList[item.type-1]}}</i>
                <i>{{item.days}}天</i>
              </p>
            </div>
          </div>

          <!--提示信息-->
          <div class="preMsgList mb4">
            <div v-for="item in model.pricePlanAdditionalInfoList"  v-if="item.additionalInfoType!=8 && item.content">
              <span>{{additionalInfoTypeList[item.additionalInfoType-1]}}</span>
              <p v-if="item.content && item.additionalInfoType!=8">{{item.content}}</p>
            </div>
            <div v-if="model.guaranteeFee">
              <span>担保信息</span>
              <p>当前房间紧张，需要提供{{model.currency}}{{model.guaranteeFee}}的信用担保，如订单不确认将立即解冻担保金额，该订单确认后不可被取消或修改，若未入住，担保费用不予退还。</p>
            </div>
            <div v-if="model.guaranteeFee&&model.guaranteeMethod">
              <span>担保方式</span>
              <p>{{guaranteeMethodArr[model.guaranteeMethod-1]}}</p>
            </div>
          </div>
        </dl>
        <div class="content_line w90"></div>
        <h3 class="To_travel_h301 fnt2">
          其他信息
        </h3>
        <dl class="bg To_travel_dl01 bg01">
          <div class="preMsgList preMsgList-chg mt1">
            <div><span>订单号</span>
              <p>{{model.orderCode}}<em class="copyBtn" @click="copyCode(model.orderCode)">复制</em></p></div>
            <div><span>联系电话</span>
              <p>{{model.contacterDTO&&model.contacterDTO.contactPhone}}</p></div>
          </div>
        </dl>
        <p class="h100"></p>
        <p class="h100"></p>
        <footer class="orderFill_bottom_p waitPay">
          <router-link class="" to="/index">继续预订</router-link>
          <router-link class="" to="/personal">我的订单</router-link>
          <a v-if="model.buttonType==2" @click="payNow">立即支付</a>
        </footer>
      </div>
    </div>
</div>

<img v-show="!first" class="first" src="../assets/bg_blank.jpg" alt="">
    <div class="hide_body02"></div>
    <!--费用明细-->
    <transition name="slideUD">
      <div class="bedmsg" v-if="iScostDetail">
        <header class="per_center mb0 head_box">
          <div class="pre_title"><a class="icon-ArtboardCopy" @click="iScostDetail=false"></a></div>
          <h3 class="mt35">费用明细</h3>
        </header>
        <section class="ds-body">
          <div class="ds-scoll">
            <ul class="cost-d-list clearfix">
              <li>
                <span>房费</span>
              </li>
              <li v-for="item in model.roomRateDetailDTOList">
                <span>{{item.saleDate}}</span>
                <span><i class="ft1">{{model.currency}}</i> {{item.salePrice}} × {{model.roomQuantity}} </span>
              </li>
              <li class="mb15">
                <span>房费小计</span>
                <span><i class="ft1">{{model.currency}}</i>{{roomCost}}</span>
              </li>
            </ul>
            <ul class="cost-d-list clearfix" v-if="model.additionChargeDTOList.length>0">
              <li>
                <span>附加费</span>
              </li>
              <li v-for="(item2,idx) in model.additionChargeDTOList">
                <span>{{DTOlist[item2.type-1]}}</span>
                <span>
                  <i class="ft1">{{model.currency}}</i> {{item2.price}} × {{item2.num}}{{unitList[item2.type-1]}} × {{item2.days}}天 </span>
              </li>
              <li class="mb15">
                <span>附加费小计</span>
                <span><i class="ft1">{{model.currency}}</i>{{addtionCost}}</span>
              </li>
            </ul>
            <div class="Totalnum">
              <span>总计</span>
              <span><i class="ft1">{{model.currency}}</i>{{totalCost}}</span>
            </div>
            <div style="height: 1rem;"></div>
          </div>
        </section>
        <div to="/pay" class="ds-foot" @click="iScostDetail=false">关闭</div>
      </div>
    </transition>


    <!--联系酒店-->
    <modal class="modal2" @close="isCall=false" :isShow="isCall" :iShead="false" :onOk="callTel">
      <div class="modal-txt">您确定联系 {{model.hotelTelPhone}}</div>
    </modal>

    <!-- 地图 -->
    <transition name="slideUD">
      <div class="login_layout search_map" v-if="isMap">
        <a class="back_btn" @click="isMap=false"></a>
        <div id="map"></div>
      </div>
    </transition>
  </div>
</template>

<style scoped>
.colorA1 {
  color: #a1a1a1 !important;
}

.bt1 {
  border-top: 1px solid #dadfe2;
  padding-top: 0.3rem;
}

.order_num {
  border-bottom: none;
}

.head_box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.w90 {
  width: 90% !important;
  margin: 0.3rem auto;
}

.mt1 {
  margin-top: 0.1rem !important;
}

.login_layout {
  display: block;
}

#map {
  width: 100%;
  height: 100%;
}
</style>
<script>
var myscroll;
import { commonInit, currencyFomate, getnotice } from "../common";
import moment from "moment";
// import PullTo from "vue-pull-to";
export default {
  // components: {
  // PullTo
  // },
  data() {
    return {
      first:false,
      isCall: false,
      isMap: false,
      payDom:false,
      iScostDetail: false,
      orderType: this.$route.params.orderCode,
      model: "",
      payDate:"",
      roomNight: "",
      DTOlist: ["加床", "加早", "网费", "门票", "其他"],
      additionalInfoTypeList: [
        "特别提示",
        "提示信息",
        "促销信息",
        "预订条款",
        "入住条款",
        "取消条款",
        "预订间数条款"
      ],
      unitList: ["张/天", "份/天", "", "张", ""],
      totalCost: 0,
      roomCost: 0,
      addtionCost: 0,
      state: "",
      stateList: ["待支付，请在30分钟内支付", "等待酒店确认", "酒店已确认", "订单已取消", "酒店已确认,财务已挂账"],
      guaranteeMethodArr: [
        "使用我的信用额度为订单担保,若客户未入住,挂入我的账单",
        "使用信用卡为订单担保",
        "使用线下支付为订单担保"
      ]
    };
  },

  filters: {
    filtersDome(date) {},
    currencyFomateFilter(type) {
      return currencyFomate(type + 1);
    },
    dateFilter(date) {
      if (date) {
        var dataArr = date.split("-");
        var str = dataArr[1] + "-" + dataArr[2];
        return str;
      }
    }
  },
  watch: {
    // abc(val) {}
  },
  mounted: function() {},
  created: function() {
    commonInit();
    var _t= this;
    if(this.$route.params.timeflag==1){
      setTimeout(function(){
        _t.first = true;
      },1000)
      setTimeout(function(){
        _t.Qinfo();
      },2500)
    }else{
      _t.first = true;
      _t.Qinfo();
    }
  },
  mounted: function() {},

  methods: {
    /*req-res*/
    Qinfo() {
      var _t = this;
      var data = this.$route.params.orderCode;
      this.$ajax.get("/hotel/order/queryOrderDetail?orderCode=" + data).then(function(res) {
          if (res.data.result == 1) {
            _t.model = res.data.model;
            var title = res.data.model.hotelName;
            document.title = title;
            //晚数计算
            _t.getstate();
            /*附加项费用*/
            _t.model.additionChargeDTOList.forEach(function(val, idx) {
              _t.totalCost += val.price * val.num * val.days;
              _t.addtionCost += val.price * val.num * val.days;
            });
            /*房间费用*/
            _t.model.roomRateDetailDTOList.forEach(function(val, idx) {
              _t.totalCost += val.salePrice * _t.model.roomQuantity;
              _t.roomCost += val.salePrice * _t.model.roomQuantity;
              console.log(_t.roomCost);
            });
            _t.roomNight = moment(_t.model.checkOutDate).diff(moment(_t.model.checkIndate),"days");
          }
        })
        .catch(function(err) {});
    },

    payNow() {
      var host = window.location.host;
      window.location.href = 'http://' + host + '/miceMobile/payPage.html?orderCode='+this.model.orderCode+'&token='+this.model.token;
    },

    getstate() {
      //console.log('getstate')
      //balanceMethod 123非单结 4单结 +  payMethodKey 1 现付 2 预付 3 现付无佣金
      //orderStateKey 1新建 2处理中 3已确认 4已取消 + payStateKey 1 待支付 2 已支付
      //现付 预付非单结
      var _t = this;
      var md = this.model;
      if (md.payMethodKey == 3 ||(md.payMethodKey == 2 && md.balanceMethod != 4)) {
        if (md.orderStateKey == 1 || md.orderStateKey == 2) {
          _t.state = 1; //待确认
        } else if (md.orderStateKey == 3) {
          if (md.payMethodKey == 2 && md.balanceMethod != 4) {
            _t.state = 4; //已挂账
          } else {
            _t.state = 2; //已确认
          }
        } else if (md.orderStateKey == 4) {
          _t.state = 3; //已取消
        }
      }
      //预付单结 payMethodKey2 balanceMethod4
      if (md.payMethodKey == 2 && md.balanceMethod == 4) {
        if (md.orderStateKey == 1 || md.orderStateKey == 2) {
          if (md.payStateKey == 2) {
            _t.state = 1; //待确认
          } else if (md.payStateKey == 1) {
            _t.state = 0; //待支付
          }
        } else if (md.orderStateKey == 3) {
          _t.state = 2; //已确认
        } else if (md.orderStateKey == 4) {
          _t.state = 3; //已取消
        }
      }
        console.log(_t.state, "this.state");
    },

    /*model*/

    getMap() {
      var data = {
        lat: this.model.latBaidu,
        lng: this.model.lngBaidu,
        address: this.model.hotelAddress,
        hotelName: this.model.hotelName
      };
      this.$router.push({
        path: "/map",
        query: data
      });
      //console.log("---");
    },

    copyCode(orderCode) {
      function Copy(str) {
        var save = function(e) {
          e.clipboardData.setData("text/plain", str);
          e.preventDefault();
        };
        document.addEventListener("copy", save);
        document.execCommand("copy");
        document.removeEventListener("copy", save);
        getnotice("复制成功");
      }

      Copy(orderCode);
    },

    callTel() {
      window.location.href = "tel:" + this.model.hotelTelPhone;
    },
  }
}
</script>



